<template>
  <div>
    <h1 class="title" :lang="lemma.language">{{ lemma.lemma }}</h1>
    <h2 class="subtitle">{{ lemma.language | humanizeLanguage }}, {{ lemma.part_of_speech | humanizePartOfSpeech }}</h2>

    <div class="columns">
      <div class="column is-one-quarter">
        <aside class="menu">
          <!-- p class="menu-label">General</p -->
          <ul class="menu-list">
            <li><a v-link="{ name: 'definition' }">Definition</a></li>
            <li><a v-link="{ name: 'lemma-tokens' }">Concordance</a></li>
            <li><a v-link="{ name: 'paradigm' }">Paradigm</a></li>
            <li><a v-link="{ name: 'chronology' }">Chronology</a></li>
            <li><a v-link="{ name: 'valency' }" v-if="lemma.valency">Valency</a></li>
          </ul>
        </aside>
      </div>

      <div class="column">
        <router-view class="view" transition="test" transition-mode="out-in" keep-alive></router-view>
      </div>
    </div>


    <!-- h3 class="subtitle is-7">Homographs</h3>

    <div class="box" v-for="homograph in lemma.homographs">
      <article class="media">
        <div class="media-left">
          <figure class="image is-64x64">
            <img src="http://placehold.it/128x128" alt="Image">
          </figure>
        </div>
        <div class="media-content">
          <div class="content">
            <p>
              <strong>John Smith</strong> <small>@johnsmith</small> <small>31m</small>
              <br>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean efficitur sit amet massa fringilla egestas. Nullam condimentum luctus turpis.
            </p>
          </div>
          <nav class="level">
            <div class="level-left">
              <a class="level-item">
                <span class="icon is-small"><i class="fa fa-reply"></i></span>
              </a>
              <a class="level-item">
                <span class="icon is-small"><i class="fa fa-retweet"></i></span>
