    </div -->
  </div>
</template>

<script>
import api from './api'
import filterMixin from './filters'

export default {
  name: 'LemmaView',

  mixins: [filterMixin],

  data() {
    return {
      schema: {},
      dictionaries: [],
      lemma: {}
    }
  },

  route: {
    data({ to, abort }) {
      let lemmaParams = {
        language: to.params.language,
        lemma: to.params.lemma,
        part_of_speech: to.params.part_of_speech
      }

      return Promise.all([
        api.getLemma(lemmaParams),
        api.getSchema(),
        api.getDictionaries()
      ]).then(function(data) {
        return {
          lemma: data[0],
          schema: data[1],
          dictionaries: data[2]
        }
      }).catch(error => {
        api.handleAPIError(error)
        abort()
      })
    }
  }
}
</script>



