import Breadcrumb from 'vue-bulma-breadcrumb'

export default {
  components: {
    Breadcrumb
  },

  data() {
    return {
    }
  },

  computed: {
    name() {
      return this.$route.name || this.$route.title
    },

    list() {
      return [
        {
          label: 'Home',
